import React from "react";
import Link from "gatsby-link";
import { fetchAcfProjects, fetchProjects, fetchTaxonomies } from "../utils/fetch-wp";
import get from "lodash.get";
import { parseQueryString } from "../utils/parse-query-string";

import SelectFilter from "../components/select-filter";

import Select from "react-select";

const SelectOld = ({ options, ...props }) => (
  <select>
    {options.map(option => (
      <option value={option} key={option} {...props}>
        {option}
      </option>
    ))}
  </select>
);

class Work extends React.Component {
  constructor(props) {
    super(props);

    const qs = parseQueryString(this.props.location.search);

    this.state = {
      data: null,
      projectTags: [],
      partnerTags: [],
      selectedTags: [],
      selectedPartners: []
      // option: ""
    };
  }

  componentDidMount() {
    fetchProjects().then(json => this.setState({ data: json }));
    fetchTaxonomies().then(({ partnerTags, projectTags }) =>
      this.setState({
        projectTags,
        partnerTags
      })
    );
  }

  render() {
    const { data, projectTags, partnerTags } = this.state;
    const projectTagNames = Object.keys(projectTags);
    const partnerTagNames = Object.keys(partnerTags);

    const selectProjectOptions = projectTagNames.map(tag => ({ value: tag, label: tag }));
    const selectPartnerOptions = partnerTagNames.map(tag => ({ value: tag, label: tag }));

    console.log(data);

    let grid = null;
    if (data) {
      grid = data.map(project => (
        <div className="col--xs-6 col--sm-4 col--md-4 project-grid">
          <Link className="project-grid__link" to={`/work/${project.slug}/`}>
            <figure key={project.id} className="project-grid__figure">
              <img
                className="project-grid__image"
                src={get(project, "acf.banner.image.sizes.medium")}
              />
              <figcaption className="project-grid__caption">
                <div className="project-grid__title">{get(project, "acf.heading.headline")}</div>
                <div className="project-grid__description">
                  {get(project, "acf.heading.subtitle")}
                </div>
              </figcaption>
            </figure>
          </Link>
        </div>
      ));
    }

    return (
      <div className="nav-offset">
        <div className="container">
          <h1 className="text-center">Our Work</h1>
        </div>

        <div className="container">
          <div className="row">
            <SelectFilter className="col--xs-6" placeholder="Tags" options={selectProjectOptions} />
            <SelectFilter
              className="col--xs-6"
              placeholder="Partners"
              options={selectPartnerOptions}
            />
          </div>
        </div>

        <div className="container">
          <div className="section row--justify-center">{grid}</div>
        </div>
      </div>
    );
  }
}

export default Work;



