import React from "react";
import Link from "gatsby-link";
import { fetchAcfProjects, fetchProjects, fetchTaxonomies } from "../utils/fetch-wp";
import get from "lodash.get";
import { parseQueryString } from "../utils/parse-query-string";
import Select from "react-select";

export default class SelectFilter extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      selectedOption: "",
      options: props.options.map(option => ({ value: option, label: option }))
    };

    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(selectedOption) {
    this.setState({ selectedOption });
  }

  render() {
    const { selectedOption } = this.state;
    const { options, className, placeholder } = this.props;

    return (
      <div className={className}>
        <Select
          placeholder={placeholder || "Select..."}
          multi={true}
          value={selectedOption}
          onChange={this.handleChange}
          options={options}
        />
      </div>
    );
  }
}



